/* eslint-disable react/prop-types */
/* eslint-disable react/display-name */
/**
 * System imports
 ============================================================================ */
import React from "react";
import { Container, Row, Col } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
import SubmitButton from "../../../submitButton";
import Card from "../../../card";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "LeadsLogIn" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  const { setRedirectTo } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Return
    ============================================================================ */
  return (
    <div className="bd-masthead">
      <Card
        backgroundHeader="Transparent"
        backgroundBody="Transparent"
        border="0"
        header={false}
      >
        <h1>Join the local currency markets</h1>

        <p className="lead">
          Quickly access local and regional market environments with
          Local-Currency.com, the worldâ€™s first local trading management
          platform, featuring one-click-allowance, instant transfers, business
          toolbox, and powerful currency management.
        </p>

        <br />

        <Container fluid>
          <Row>
            <Col style={{ paddingLeft: 0 }}>
              <SubmitButton
                {...args}
                caption="Get started"
                request="get started"
                namespace="LogIn"
                onSubmit={() => setRedirectTo("/sign-up")}
              />
            </Col>
            <Col style={{ paddingRight: 0 }}>
              <SubmitButton
                {...args}
                caption="Readme"
                request="get started"
                namespace="LogIn"
                variant="secondary"
                onSubmit={() => setRedirectTo("/article/readme")}
              />
            </Col>
          </Row>
        </Container>

        <br />
        <br />
        <br />
      </Card>
    </div>
  );
};
